import "../styles/TransporterForm.css"

import { motion } from "framer-motion"

import React, { useState, useEffect } from 'react';

import exit from "../asstes/dashboard/exit.png"

import { getDatabase, ref, set, push, get } from "firebase/database";



function ServiceForm({setUpdateServiceView, close  , update}) {

    

    const [name, setName] = useState("");
    const [price, setPrice] = useState("");


    const clear = ()=> {
        setName("");
        setPrice("");
    }


    const submit = ()=> {

        const db = getDatabase();
        push(ref(db, 'services'), {
            name: name,
            price : price
            
        });

        console.log("insert service")
        setUpdateServiceView(true)
        update()
        clear();
    }


    return (
        <motion.div
            className="transporter-Form"
            initial={{ opacity: 0 }}
            animate={{ opacity: 1, transition: { delay: 2 } }}
        >

            <div className="exit-btn" 
                onClick={()=> {
                    close()
                }}
            >
                <img src={exit} alt="" />
            </div>
            <p>New Service</p>

            <div className="form-grp">
                <div className="form-input">
                    <p>Name</p>
                    <input type="text" placeholder="Transporter's Name" value={name}  
                    onChange={(e)=> {
                        setName(e.target.value);
                    }}/>
                </div>

                <div className="form-input">
                    <p>Price</p>
                    <input type="text" placeholder="Transporter's Name" value={price}  
                    onChange={(e)=> {
                        setPrice(e.target.value);
                    }}/>
                </div>
                
            </div>

           

            <div className="form-actions">
                <div className="form-action bkBlue"
                    onClick={()=> {
                        submit()
                        close()
                    }}
                >
                    <p>Submit</p>
                </div>

                <div className="form-action second-action" 
                    onClick={()=> {
                        clear()
                    }}
                >
                    <p>Clear</p>
                </div>

               
                
            </div>
        </motion.div>
    );
}

export default ServiceForm;