import "../styles/TransporterForm.css"

import { motion } from "framer-motion"

import React, { useState, useEffect } from 'react';

import exit from "../asstes/dashboard/exit.png"

import { getDatabase, ref, set, push, get } from "firebase/database";



function TransporterForm({setUpdateTranspView, close , list , update}) {

    const wilayaNames = [
        "Adrar", "Chlef", "Laghouat", "Oum El Bouaghi", "Batna", "Bjaa", "Biskra", "Bechar", "Blida", "Bouira",
        "Tamanrasset", "Tbessa", "Tlemcen", "Tiaret", "Tizi Ouzou", "Alger", "Djelfa", "Jijel", "Se9tif", "Saefda",
        "Skikda", "Sidi Bel Abbes", "Annaba", "Guelma", "Constantine", "Medea", "Mostaganem", "M'Sila", "Mascara",
        "Ouargla", "Oran", "El Bayadh", "Illizi", "Bordj Bou Arreridj", "Boumerdes", "El Tarf", "Tindouf", "Tissemsilt",
        "El Oued", "Khenchela", "Souk Ahras", "Tipaza", "Mila", "Ain Defla", "Naama", "Ain Temouchent", "Ghardaefa",
        "Relizane", "El M'ghair", "El Menia", "Ouled Djellal", "Bordj Baji Mokhtar", "Bni Abbs", "Timimoun",
        "Touggourt", "Djanet", "In Salah", "In Guezzam"
      ];

      const gendres = ["Man", "Women"]

    const [jobs , setJobs] = useState([]);
    const [transName, setTransName] = useState("");
    const [workerAge, setWorkerAge] = useState("");
    const [workerEmail, setWorkerEmail] = useState("");
    const [workerPhone, setWorkerPhone] = useState("");
    const [workerWilaya, setWorkerWilaya] = useState("");
    const [workerCommune, setWorkerCommune] = useState("");
    const [workerGendre, setWorkerGendre] = useState("");
    const [workerJob, setWorkerJob] = useState("");
    
    const [workerExperience, setWorkerExperience] = useState("");


    const clear = ()=> {
        setTransName("");
        setWorkerAge("");
        setWorkerEmail("");
        setWorkerPhone("");
        setWorkerWilaya("");
        setWorkerCommune("");
        setWorkerGendre("");
        setWorkerJob("");
        setWorkerExperience("");
    }


    const submit = ()=> {

        const db = getDatabase();
        push(ref(db, 'workers'), {
            name: transName,
            age: workerAge,
            willaya: workerWilaya === "" ? wilayaNames[0] : workerWilaya,
            commune: workerCommune === "" ? wilayaNames[0] : workerCommune,
            email: workerEmail,
            phone: workerPhone,
            job: workerJob === "" ? jobs[0] : workerJob,
            gendre: workerGendre === "" ? gendres[0] : workerGendre,
            Revenues:0,
            experience: workerExperience === "" ? 0 : workerExperience,
            isvalide:true,
            status:true

        });

        console.log("insert transp")
        setUpdateTranspView(true)
        update()
        clear();
    }


    const getAllServices = async ()=> {
        
        const db = getDatabase();
        const shipments = ref(db, 'services');
        let ss = await get(shipments);

        const data = ss.val();
        const list = Object.entries(data)


        var res = [];
        for (let i = 0; i < list.length; i++) {
            res.push(list[i][1].name)
        }

        setJobs(res)
        
    }


    useEffect(()=> {
        getAllServices();
    } )

    return (
        <motion.div
            className="transporter-Form"
            initial={{ opacity: 0 }}
            animate={{ opacity: 1, transition: { delay: 2 } }}
        >

            <div className="exit-btn" 
                onClick={()=> {
                    close()
                }}
            >
                <img src={exit} alt="" />
            </div>
            <p>New Worker</p>

            <div className="form-grp">
                <div className="form-input">
                    <p>Name</p>
                    <input type="text" placeholder="Worker's Name" value={transName}  
                    onChange={(e)=> {
                        setTransName(e.target.value);
                    }}/>
                </div>
                <div className="form-input">
                    <p>Age</p>
                    
                    <input type="text" placeholder="Worker's Age" value={workerAge}  
                     onChange={(e) => {
                        setWorkerAge(e.target.value);
                    }} />

                
                </div>
            </div>

            <div className="form-grp">
                <div className="form-input">
                    <p>Email</p>
                    <input type="text" placeholder="Worker's Email" value={workerEmail}
                        onChange={(e) => {
                            setWorkerEmail(e.target.value);
                        }} />
                </div>
                <div className="form-input">
                    <p>Phone</p>
                    <input type="text" placeholder="Worker's Phone" value={workerPhone}
                        onChange={(e) => {
                            setWorkerPhone(e.target.value);
                        }} />

                </div>
            </div>

            <div className="form-grp">
                <div className="form-input">
                    <p>Wilaya</p>
                    
                    
                    <select value={workerWilaya}
                            onChange={(e) => {
                                setWorkerWilaya(e.target.value);
                            }} >
                            {wilayaNames.map((method, index) => (
                            <option key={index} value={method}>
                                {method}
                            </option>
                            ))}
                    </select>

                </div>
                <div className="form-input">
                    <p>Commune</p>

                    <input type="text" placeholder="Worker's Commune" value={workerCommune}
                        onChange={(e) => {
                            setWorkerCommune(e.target.value);
                        }} />

                </div>
            </div>

            <div className="form-grp">
            <div className="form-input">
                    <p>Gendre</p>
                    <select value={workerGendre}
                            onChange={(e) => {
                                setWorkerGendre(e.target.value);
                            }} >
                            {gendres.map((method, index) => (
                            <option key={index} value={method}>
                                {method}
                            </option>
                            ))}
                    </select>

                </div>
                
                <div className="form-input">
                    <p>Job</p>

                    <select value={workerJob}
                            onChange={(e) => {
                                setWorkerJob(e.target.value);
                            }} >
                            {jobs.map((vehicle, index) => (
                            <option key={index} value={vehicle}>
                                {vehicle}
                            </option>
                            ))}
                    </select>
                </div>
            </div>

            <div className="form-grp">
                <div className="form-input">
                    <p>Experience</p>
                    <input type="text" placeholder="worker's Experience" value={workerExperience}
                        onChange={(e) => {
                            setWorkerExperience(e.target.value);
                        }} />
                </div>
                
            </div>

            

            <div className="form-actions">
                <div className="form-action bkBlue"
                    onClick={()=> {
                        submit()
                        close()
                    }}
                >
                    <p>Submit</p>
                </div>

                <div className="form-action second-action" 
                    onClick={()=> {
                        clear()
                    }}
                >
                    <p>Clear</p>
                </div>

               
                
            </div>
        </motion.div>
    );
}

export default TransporterForm;