import "../styles/dashboard.css"

import { motion } from "framer-motion"

import React, { useState, useEffect } from 'react';

import exit from "../asstes/dashboard/exit.png"
import loadingIcon from "../asstes/loadingIconBlue.gif"
import legal from "../asstes/dashboard/legal.png"

import { getDatabase, ref, set, push, get, update } from "firebase/database";



function SerciveDetails({setShowtranspPapers , service , visible , close , setShowOrdersOverlay , setSelectedOrder}) {



    const [orders , setorders] = useState([]);
    const [loading, setloading] = useState(true);

    const getClientById = async (id) => {


        const db = getDatabase();
        const shipments = ref(db, 'clients');
        let ss = await get(shipments);

        const data = ss.val();
        const list = Object.entries(data)
        for (let i = 0; i < list.length; i++) {
            let element = list[i];
            if (element[0] == id) {
                return element;
            }
        }

        return "cc";
    }


    const getWorkerById = async (id) => {


        const db = getDatabase();
        const shipments = ref(db, 'workers');
        let ss = await get(shipments);

        const data = ss.val();
        const list = Object.entries(data)
        for (let i = 0; i < list.length; i++) {
            let element = list[i];
            if (element[0] == id) {
                return element;
            }
        }

        return "cc";
    }
    const getServiceById = async (id) => {


        const db = getDatabase();
        const shipments = ref(db, 'services');
        let ss = await get(shipments);

        const data = ss.val();
        const list = Object.entries(data)
        for (let i = 0; i < list.length; i++) {
            let element = list[i];
            if (element[0] == id) {
                return element;
            }
        }

        return "cc";
    }


    const getOrderByTransp = async (id) => {

        console.log("by worker" + id)

        
        const db = getDatabase();
        const shipments = ref(db, 'offers');
        let ss = await get(shipments);

        const data = ss.val();
        const list = Object.entries(data)

        var res = [];
        for (let i = 0; i < list.length; i++) {
            let element = list[i];

            console.log(element[1].worker_id)
            if (element[1].service_id == id) {
                
                var client = await getClientById(element[1].client_id);

                    var service = await getServiceById(element[1].service_id);

                    var worker = await getWorkerById(element[1].service_id);

                    let it = element[1]
                    it.client_name = client[1].name;
                    
                    it.service_name = service[1].name;
                    it.worker_name = worker[1].name;

                    res.push(it)
                    /*
                    it.setInf = (obj, filtOrd, allOrd, presOrd) => {
                        updateItem(it, obj, filtOrd, allOrd, presOrd);
                    }
                    */
                console.log(element[1].commune + "ssssssssssssssssssssssssssssssssssss")

            }
        }

        setorders(res);
        
    }

    
    useEffect(()=> {
        if (service != null) {
            
        console.log("trans.............")
            getOrderByTransp(service.id);
        }
        console.log('serviceDtails')

        setTimeout(() => {
            setloading(false)
        }, 1000)
    } , [])

    return (

        service != null ?
        <motion.div className="transporter-details-overlay"
            initial={{ scale: 0, opacity: 0, display: "none" }}
            animate={visible ? { scale: 1, display: "flex", opacity: 1 } : { scale: 0.5, opacity: 0, transition: { ease: "easeInOut" } }}
            exit={{ display: "none" }}
            onClick={(event) => {
                event.preventDefault();
            }}
        >


                <motion.div className="preloeader"
                    initial={{ opacity: 1 }}
                    animate={loading ? { opacity: 1 } : { opacity: 0 }}
                >
                    <img src={loadingIcon} alt="" />
                </motion.div>
            <motion.div className="transporter-details-container" id="transporter-details"
                    initial={{ opacity: 0}}
                    animate={!loading ? { opacity: 1 } : { opacity: 0 }}
                
                onClick={(event) => {
                    event.preventDefault();
                }}
            >

                <div className="close-btn"
                    onClick={()=> {
                        close(false)
                    }}
                >
                    <img src={exit} alt="" />
                </div>


                <div className="transp-details-header">
                    <p>khadamat Service</p>
                    <p>{service.name}</p>


                    

                    <div className="legalisation"
                        onClick={()=> {
                            setShowtranspPapers(true)
                        }}
                    >
                        <p >Update</p>
                        <img src={legal} width ="24" height="24" alt="" />
                    </div>

                </div>

                <p>Service infos</p>

                <div className="transp-order-list">
                    <div className="transp-order-item">
                        <div>
                            <p>Num</p>
                        </div>
                        <div>
                            <p>Client</p>
                        </div>
                        <div>
                            <p>Service</p>
                        </div>
                        <div>
                            <p>wilaya</p>
                        </div>
                        <div>
                            <p>Commune</p>
                        </div>
                        <div>
                            <p>Status</p>
                        </div>
                        

                        

                        






                    </div>

                        {
                            orders.map((item , index)=> {
                                return(
                                    <div className="transp-order-item">
                                        <div>
                                            <p>{index + 1}</p>
                                        </div>
                                        <div>
                                            <p>{item.client_name}</p>
                                        </div>

                                        <div>
                                            <p>{service.name}</p>
                                        </div>
                                        <div>
                                            <p>{item.wilaya}</p>
                                        </div>
                                        <div>
                                            <p>{item.commune}</p>
                                        </div>
                                        <div>
                                            <p>{item.status}</p>
                                        </div>

                                        <div className="more-info-detail"
                                            onClick={()=> {
                                                
                                                setShowOrdersOverlay(true);
                                                setSelectedOrder(item);
                                            }}
                                        >
                                            <img src={exit}/>
                                        </div>
                                       





                                    </div>
                                )
                            })
                        }
                </div>

            </motion.div>
        </motion.div>

        :
        <div></div>
    )
}

export default SerciveDetails;