


import "../styles/dashboard.css"

import { motion } from "framer-motion"

import React, { useState, useEffect } from 'react';

import load from "../asstes/loadingIconBlue.gif"




function OfflineOverlay({ anime }) {

    return(

        <motion.div className="select-transporter-overlay cnxion-overlay"
            initial={{ scale: 0, opacity: 0, display: "none" }}
            animate={anime ? { scale: 1, display: "flex", opacity: 1 } : { scale: 0.5, opacity: 0, transition: { ease: "easeInOut" } }}
            exit={{ display: "none" }}
            onClick={(event) => {
                event.preventDefault();
            }}
        >
            <div className="select-transporter-container " id="select-transporter"
                onClick={(event) => {
                    event.preventDefault();
                }}
            >
                <p>Connection Lost</p>
                
                <p>Please Check your Internet Connection (This popup will disappear when you reconnect)</p>               
                <img src={load}/>
                

            </div>
        </motion.div>

    )
}

export default OfflineOverlay;