import "../styles/orderDetails.css"

import { motion } from "framer-motion"
import client from "../asstes/dashboard/client.png"

import React, { useState, useEffect } from 'react';

import exit from "../asstes/dashboard/exit.png"

import { getDatabase, ref, set, push, get } from "firebase/database";


import palette from "../asstes/palette.png"



function OrderDetails({order , close , setShowReviews }) {



    const [transp , setTransp] = useState(null);
    
    const [transDetailShown , setTransDetailShown] = useState(false);
    
    const getOrderTransporter = async ()=> {


            const db = getDatabase();
            const shipments = ref(db, 'Transporter');
            let ss = await get(shipments);
    
            const data = ss.val();
            const list = Object.entries(data)
            for (let i = 0; i < list.length; i++) {
                let element = list[i];


                if (element[0] == order.transporter_id) {

                    setTransp({ id: element[0], name: element[1].name, city: element[1].city, location: element[1].location, email: element[1].email, payment_method: element[1].payment_method, phone: element[1].phone, state: element[1].status, vehicle_type: element[1].vehicle, date: "12/12/2023" })
                    
                    return element;
                }
            }
    
            return "cc";
        
    }


    useEffect(() => {


        
        console.log(order + ".....................................")
        console.log(order)
        getOrderTransporter();

        

    })

    return (
        order != null ?
        <motion.div
            className="orders-details"
            id="orders-details"
            initial={{ opacity: 0 }}
            animate={transDetailShown ? { opacity: 1, transition: { delay: 2 } , zIndex:6 } : { opacity: 1, transition: { delay: 2 }, zIndex : 4 }}
        >

                <div className="close-btn"
                    onClick={()=> {
                        close(false)
                    }}
                >
                    
                    <img src={exit} alt="" />
                    </div>
            <div className="top-orders-details">
                <div className="orders-infos">
                    <p>Order</p>
                    
                    <p>{order.id}</p>
                </div>
                <div className="orders-infos">
                    <div className="order-info-header">
                        <div className="order-info-img">
                            <img src={client} alt="" />
                        </div>

                        <p>Client</p>
                    </div>

                    <div className="order-info-corps">
                        <p className="small-text"> Client {order.client_name}</p>
                        <p>{order.client}</p>
                    </div>
                </div>
                <div className="orders-infos">
                    <div className="order-info-header">
                        <div className="order-info-img">
                            <img src={client} alt="" />
                        </div>

                        <p>Wilaya</p>
                    </div>

                    <div className="order-info-corps">
                         
                        <p>{order.wilaya}</p>
                    </div>
                </div>
                <div className="orders-infos">
                    <div className="order-info-header">
                        <div className="order-info-img">
                            <img src={client} alt="" />
                        </div>

                        <p>Commune</p>
                    </div>

                    <div className="order-info-corps">
                         
                        <p>{order.commune}</p>
                    </div>
                </div>
                <div className="orders-infos">
                    <div className="order-info-header">
                        <div className="order-info-img">
                            <img src={palette} alt="" />
                        </div>

                        <p>Service</p>

                        
                    </div>

                    <div className="order-info-corps">
                        <p>{order.service_name}</p>
                    </div>
                </div>

            </div>
            <div className="bottom-orders-details">
                <div className="orders-infos">
                    <p>Order State</p>

                    <motion.p className="state-container">
                        
                    </motion.p>
                    
                    <p className="big-text">{order.status}</p>
                    

                    {
                         order.state === "w" ?
                         null
                         :
                         <div>
                            <div className="btn bkBlue"
                                onClick={()=> {
                                    setShowReviews(true);
                                }}
                            >
                                <p>Reviews</p>
                                
                            </div>
                            
                        </div>
                    }


                </div>
                <div className="orders-infos">
                    <div className="order-info-img">
                        <img src={client} alt="" />
                    </div>
                    <p>Worker</p>
                    <div className="order-info-corps">
                         
                        <p>{order.worker_id == "not yet" ? "not yet" : order.worker_name}</p>
                    </div>
                </div>
                <div className="orders-infos last-order-info">
                    <div className="order-info-img">
                        <img src={client} alt="" />
                    </div>
                    <p>Description</p>
                    <div className="order-info-corps">
                         
                        <p>{order.description}</p>
                    </div>
                </div>
                
            </div>

            

            
        </motion.div>
        :
        <div></div>
    );
}

export default OrderDetails;