


import "../styles/dashboard.css"

import { motion } from "framer-motion"


import React, { useState, useEffect } from 'react';

import GoogleMapReact from 'google-map-react';
import {MarkerF} from '@react-google-maps/api'



import exit from "../asstes/dashboard/exit.png"


import marker from "../asstes/marker.png"

const AnyReactComponent = ({ text }) => <div className="marker">{text}</div>;



function SimpleMap({order , anime , close , lat , lng}){


  const defaultProps = {
    


    center: {
      lat: order.lat,
      lng:  order.lng
    },
    zoom: 16
  };

  useEffect(()=> {

    console.log("map.....");
    console.log(order)
    
    console.log(order.lat)
    
  } )

  return (
    // Important! Always set the container height explicitly

    order.lat === "user denied location" ? 

    <motion.div className="select-transporter-overlay blur"
        initial={{ scale: 0, opacity: 0, display: "none" }}
        animate={anime ? { scale: 1, display: "flex", opacity: 1 } : { scale: 0.5, opacity: 0, transition: { ease: "easeInOut" } }}
        exit={{ display: "none" }}
         >



      <div className="location-denied">


      <div className="close-btn"
                    onClick={()=> {
                        close(false)
                    }}>
        <img src={exit} alt="" />
      </div>



      <p>Khadamat Client Localisation</p>
      <p>Client Denied GPS location</p>

      <div className="conf-action"
          onClick={() => {
            close(false)
        }}>
          <p>Close</p>
        </div>
      </div>
      


      
      </motion.div>

    :
    <motion.div className="select-transporter-overlay "
        initial={{ scale: 0, opacity: 0, display: "none" }}
        animate={anime ? { scale: 1, display: "flex", opacity: 1 } : { scale: 0.5, opacity: 0, transition: { ease: "easeInOut" } }}
        exit={{ display: "none" }}
         >


      <div style={{width:"50%" , height: "80%" , borderRadius:"5px" , overflow:"hidden", position:"relative"}}>

      <div className="close-btn left-exit"
                    onClick={()=> {
                        close(false)
                    }}
                ><img src={exit} alt="" />
                </div>
      <GoogleMapReact
        bootstrapURLKeys={{ key: "AIzaSyA_wNuCzia92MAmdLRzmqitRGvCF7wCZPY" }}
        defaultCenter={defaultProps.center}
        defaultZoom={defaultProps.zoom}
        center = { defaultProps.center }
      >

        

       





      </GoogleMapReact>


      
      </div>
      </motion.div>

  );
}


export default SimpleMap;