import "../styles/dashboard.css"

import { motion } from "framer-motion"

import React, { useState, useEffect } from 'react';

import exit from "../asstes/dashboard/exit.png"


import more from "../asstes/more.png"
import loadingIcon from "../asstes/loadingIconBlue.gif"
import legal from "../asstes/dashboard/legal.png"

import { getDatabase, ref, set, push, get, update } from "firebase/database";



function Reviews({setShowtranspPapers , order , visible , close , setShowOrdersOverlay , setSelectedOrder}) {



    const [orders , setorders] = useState([]);
    const [loading, setloading] = useState(true);

    
    const [reviewsList , setReviewsList] = useState([]);
    

    const getClientById = async (id) => {

        const db = getDatabase();
        const shipments = ref(db, 'clients');
        let ss = await get(shipments);

        const data = ss.val();
        const list = Object.entries(data)
        for (let i = 0; i < list.length; i++) {
            let element = list[i];
            if (element[0] === id) {

                return element;
            }
        }

        return "cc";
    }

    


    const getOrderReviews = async()=> {
        const db = getDatabase();
        const shipments = ref(db, 'reviews');
        let ss = await get(shipments);

        const data = ss.val();
        const list = Object.entries(data)

        var res = [];
        var review ;


            for (let index = 0; index < list.length; index++) {
                const item = list[index];
                
            
            if (order.id === item[1].offer_Id) {


                review = item[1];
                var client = await getClientById(item[1].cliend_Id);
                console.log(client[1].name);
                review.client_name = client[1].name;
                console.log(review);
                res.push(review);
            }
        }

        
        setloading(false)
        setReviewsList(res);
    }
    
    
    useEffect(()=> {
        
        console.log( order)
        if (order != null) {
            
            getOrderReviews();
        }


    } , [])

    return (

        order != null ?
        <motion.div className="transporter-details-overlay review-container"
            initial={{ scale: 0, opacity: 0, display: "none" }}
            animate={visible ? { scale: 1, display: "flex", opacity: 1 } : { scale: 0.5, opacity: 0, transition: { ease: "easeInOut" } }}
            exit={{ display: "none" }}
            onClick={(event) => {
                event.preventDefault();
            }}
        >


                <motion.div className="preloeader"
                    initial={{ opacity: 1 }}
                    animate={loading ? { opacity: 1 } : { opacity: 0 }}
                >
                    <img src={loadingIcon} alt="" />
                </motion.div>
            <motion.div className="transporter-details-container" id="transporter-details"
                    initial={{ opacity: 0}}
                    animate={!loading ? { opacity: 1 } : { opacity: 0 }}
                
                onClick={(event) => {
                    event.preventDefault();
                }}
            >

                <div className="close-btn"
                    onClick={()=> {
                        close()
                    }}
                >
                    <img src={exit} alt="" />
                </div>


                <div className="transp-details-header">
                    <p>khadamat Reviews</p>
                    <p>{order.id}</p>


                    

                    

                </div>

                <p>Order Reviews : {reviewsList.length} </p>

                <div className="transp-order-list review-list">
                <div className="transp-order-item">
                                        <div>
                                            <p>Num</p>
                                        </div>
                                        <div>
                                            <p>Client</p>
                                        </div>
                                        <div className="review-desc">
                                            <p>Description</p>
                                        </div>

                                    </div>

                        {
                            reviewsList.map((item , index)=> {
                                return(
                                    <div className="transp-order-item">
                                        <div>
                                            <p>{index + 1}</p>
                                        </div>
                                        <div>
                                            <p>{item.client_name}</p>
                                        </div>

                                        <div className="review-desc">
                                            <p>{item.description}</p>
                                        </div>
                                        

                                       





                                    </div>
                                )
                            })
                        }
                </div>

            </motion.div>
        </motion.div>

        :
        <div></div>
    )
}

export default Reviews;